import { getRootUrl } from './rootUrl';
import axios from 'axios';


export const loadHistory = (token, taskId) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  return axios.get(
    rootUrl + "/taskHistory/getTaskHistory?typeId="+taskId,
    config).then(response => response.data)
};
export const downloadFile = (token,fileName, fileId) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    },
    responseType: 'blob'
  };

  axios.get(rootUrl + `/taskHistory/download/${fileId}`, config)
    .then(response => {
      // Extract filename from response headers


      // const contentDisposition = response.headers['content-disposition'];
      // const fileNameMatch = contentDisposition && contentDisposition.match(/filename="(.+?)"/);
      // const fileName = fileNameMatch && fileNameMatch[1];

      // Create a URL object from the response data
      const url = window.URL.createObjectURL(new Blob([response.data]));

      // Create a link element
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName); // Set the file name
      // link.setAttribute('type', response.headers['content-type']); // Set the file type

      // Append the link to the document body and click it to trigger download
      document.body.appendChild(link);
      link.click();

      // Remove the link element from the document body
      document.body.removeChild(link);
    })
    .catch(error => {
      // Handle error
      console.error('Error downloading file:', error);
    });
};
