import { useState } from "react";
import { useStore } from "src/MobxStore";
import { updateRemark } from '../../services/inspectionClient';
import { CCol, CModal, CModalBody, CModalFooter, CModalHeader,CLabel,CTextarea } from "@coreui/react";
import { DialogContentText } from "@material-ui/core";
import { NotificationManager } from "react-notifications";

const EditRemark = ({ show, onClose, onEdit, inspection }) => {
    const store = useStore();
    const [remark, setRemark] = useState(inspection.remark)
    const [loading, setLoading] = useState(false);

    const handleUpdateRemark = () => {
        setLoading(true);
        const request={
            remark:remark
        }
        updateRemark(store.login.token,inspection.id, request).then(response => {
            if (response.status) {
                NotificationManager.success(response.message, 'Success', 3000);
                setLoading(false);
                onEdit();
            } else {
                NotificationManager.error(response.message, 'Failed!', 3000);
                setLoading(false);
            }
        }).catch(err => {
            setLoading(false);
            NotificationManager.error("Check your connection", "Network Error", 3000);
        });
    }
    return (
        <CModal
            show={show}
            onClose={onClose}
            size='lg'
            backdrop
            close
            closeOnBackdrop={false}
        >
            <CModalHeader size="modal-lg" closeButton >
                <h5 style={{
                    textAlign: "center",
                    fontSize: 20,
                    fontWeight: "bold"
                }}>Edit Remark
                </h5></CModalHeader>
            <CModalBody>
                <CCol
                >
                    <CLabel htmlFor="edit-remark">Remark</CLabel>
                    <CTextarea
                        id="edit-remark"
                        name="edit-remark"
                        rows={3}
                        value={remark}
                        onChange={(e) => {
                            setRemark(e.target.value);
                        }}
                    />
                </CCol>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex justify-content-between">
                    <button type="button" className="btn btn-outline-danger m-2 btn-sm" onClick={onClose}>
                        Close
                    </button>

                    <button
                        disabled={loading}
                        className="btn btn-outline-success m-2 btn-sm"
                        type="submit"
                        onClick={(e) => handleUpdateRemark(e)}>
                        Edit
                    </button>

                </div>
            </CModalFooter>
        </CModal>
    );
}
export default EditRemark;